import React from "react";
const Header = () => (
  <nav class="navbar has-shadow is-fixed-top">
    <div class="navbar-brand">
      <a
        class="navbar-item navbar-logo"
        href="https://nirzaq.github.io/selebtwit/"
      >
        <img
          src="assets/twitter_circle_logo.svg"
          alt="Selebtwit Statistics Analysis"
          width="50"
          height="28"
        />{" "}
        #SSA
      </a>
      <div
        class="navbar-burger burger"
        data-target="navbarExampleTransparentExample"
      >
        <span />
        <span />
        <span />
      </div>
    </div>

    <div id="navbarExampleTransparentExample" class="navbar-menu">
      <div class="navbar-start">
        <a class="navbar-item" href="https://nirzaq.github.io/selebtwit">
          Indonesia
        </a>
        <a class="navbar-item" href="https://nirzaq.github.io/selebtwit/world">
          World
        </a>
        <a class="navbar-item" href="https://nirzaq.github.io/selebtwit/faq">
          FAQ
        </a>
      </div>

      <div class="navbar-end">
        <div class="navbar-item" />
      </div>
    </div>
  </nav>
);

export default Header;



